import logo from "./logo.svg";
import "./App.css";

import { useState, useEffect } from "react";

function App() {
  let [customers, setCustomers] = useState([]);

  useEffect(() => {
    async function fetchIt() {
      let hr = await fetch("https://munbackend.herokuapp.com/customers");
      let customers = await hr.json();
      setCustomers(customers);
    }
    fetchIt();
  }, []);

  let ui = customers.map((customer) => (
    <li key={customer.id}>{customer.name}</li>
  ));
  return (
    <>
      <h1>Customers</h1>
      <ul>{ui}</ul>
    </>
  );
}

export default App;
